import React, { useState, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { courseAPI, handleApiError } from '../services/api';
import toast from 'react-hot-toast';
import { useApp } from '../App'; // Assuming useApp is exported from App.jsx

import LoadingSpinner from './LoadingSpinner';
import ErrorFallback from './ErrorFallback';
import { BookOpen, ChevronRight, Clock, BarChart, CheckCircle, Lock, PlayCircle } from 'lucide-react';

// A simple progress bar component
const ProgressBar = ({ value }) => {
  const percentage = Math.round(value);
  return (
    <div>
      <div className="flex justify-between mb-1">
        <span className="text-sm font-medium text-gray-700">Course Progress</span>
        <span className="text-sm font-medium text-blue-700">{percentage}%</span>
      </div>
      <div className="w-full bg-gray-200 rounded-full h-2.5">
        <div 
          className="bg-blue-600 h-2.5 rounded-full transition-all duration-500" 
          style={{ width: `${percentage}%` }}
        ></div>
      </div>
    </div>
  );
};

const CourseDetail = () => {
  const { courseId } = useParams();
  const navigate = useNavigate();
  const { user } = useApp();

  const [course, setCourse] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [enrolling, setEnrolling] = useState(false);

  useEffect(() => {
    const fetchCourseDetails = async () => {
      try {
        setLoading(true);
        const courseData = await courseAPI.getCourseById(courseId);
        setCourse(courseData);
      } catch (err) {
        handleApiError(err);
        setError(err);
      } finally {
        setLoading(false);
      }
    };

    fetchCourseDetails();
  }, [courseId]);

  const handleEnroll = async () => {
    setEnrolling(true);
    try {
      await courseAPI.enrollInCourse(courseId);
      // Refetch course data to update enrollment status
      const updatedCourseData = await courseAPI.getCourseById(courseId);
      setCourse(updatedCourseData);
      toast.success(`Successfully enrolled in "${updatedCourseData.title}"!`);
    } catch (err) {
      handleApiError(err);
    } finally {
      setEnrolling(false);
    }
  };

  const handleStartLesson = (lessonId) => {
    navigate(`/course/${courseId}/lesson/${lessonId}`);
  };

  if (loading) {
    return <div className="flex justify-center items-center h-screen"><LoadingSpinner /></div>;
  }

  if (error) {
    return <ErrorFallback error={error} />;
  }

  if (!course) {
    return <div className="text-center py-10">Course not found.</div>;
  }

  const isEnrolled = course.is_enrolled;
  const totalLessons = course.lessons.length;
  const completedLessons = course.lessons.filter(l => l.is_completed).length;
  const progress = totalLessons > 0 ? (completedLessons / totalLessons) * 100 : 0;

  return (
    <div className="container mx-auto p-4 md:p-8">
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* Left Column - Course Content */}
        <div className="lg:col-span-2">
          <div className="bg-white p-6 rounded-lg shadow-lg">
            {/* Breadcrumbs */}
            <nav className="text-sm font-medium text-gray-500 mb-4">
              <Link to="/courses" className="hover:text-blue-600">Courses</Link>
              <ChevronRight className="inline w-4 h-4 mx-1" />
              <span>{course.title}</span>
            </nav>

            {/* Title and Description */}
            <h1 className="text-3xl md:text-4xl font-bold text-gray-900 mb-2">{course.title}</h1>
            <p className="text-gray-600 mb-6">{course.description}</p>

            {/* Course Meta */}
            <div className="flex flex-wrap items-center gap-x-6 gap-y-2 text-sm text-gray-500 mb-8">
              <div className="flex items-center">
                <BookOpen className="w-4 h-4 mr-1.5" /> {totalLessons} Lessons
              </div>
              <div className="flex items-center">
                <Clock className="w-4 h-4 mr-1.5" /> Approx. {course.duration_hours} hours
              </div>
              <div className="flex items-center">
                <BarChart className="w-4 h-4 mr-1.5" /> {course.difficulty_level}
              </div>
            </div>

            {/* Lessons List */}
            <div className="space-y-4">
              <h2 className="text-2xl font-bold text-gray-800 mb-4">Course Content</h2>
              {course.lessons.map((lesson, index) => (
                <div key={lesson.id} className="border rounded-lg overflow-hidden transition-shadow hover:shadow-md">
                  <div className="p-4 flex justify-between items-center">
                    <div className="flex items-center">
                      <div className="mr-4 text-blue-500 font-bold text-lg">{index + 1}</div>
                      <div>
                        <h3 className="font-semibold text-gray-800">{lesson.title}</h3>
                        <p className="text-sm text-gray-500">{lesson.duration_minutes} mins</p>
                      </div>
                    </div>
                    {isEnrolled ? (
                      <button
                        onClick={() => handleStartLesson(lesson.id)}
                        className="flex items-center px-4 py-2 text-sm font-medium text-white bg-blue-600 rounded-lg hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 transition-colors"
                      >
                        {lesson.is_completed ? (
                          <CheckCircle className="w-4 h-4 mr-2" />
                        ) : (
                          <PlayCircle className="w-4 h-4 mr-2" />
                        )}
                        {lesson.is_completed ? 'Review' : 'Start'}
                      </button>
                    ) : (
                      <Lock className="w-5 h-5 text-gray-400" />
                    )}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Right Column - Course Info & Actions */}
        <div className="lg:col-span-1">
          <div className="bg-white p-6 rounded-lg shadow-lg sticky top-8">
            <img src={course.thumbnail_url || 'https://placehold.co/600x400/E2E8F0/4A5568?text=Course'} alt={course.title} className="rounded-lg mb-4 w-full object-cover" />
            
            {isEnrolled ? (
              <div className="space-y-4">
                <h3 className="text-xl font-bold text-gray-800">You are enrolled!</h3>
                <ProgressBar value={progress} />
                <button
                  onClick={() => handleStartLesson(course.lessons[0]?.id)}
                  className="w-full flex justify-center items-center px-4 py-3 text-sm font-medium text-white bg-green-600 rounded-lg hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-green-500 transition-colors"
                >
                  <PlayCircle className="w-5 h-5 mr-2" />
                  {completedLessons > 0 ? 'Continue Learning' : 'Start Course'}
                </button>
              </div>
            ) : (
              <div className="space-y-4">
                <p className="text-2xl font-bold text-gray-900">{course.price === 0 ? 'Free' : `$${course.price}`}</p>
                <button
                  onClick={handleEnroll}
                  disabled={enrolling}
                  className="w-full flex justify-center items-center px-4 py-3 text-sm font-medium text-white bg-blue-600 rounded-lg hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:opacity-50 transition-colors"
                >
                  {enrolling ? <LoadingSpinner size="small" /> : 'Enroll Now'}
                </button>
                <p className="text-xs text-gray-500 text-center">30-Day Money-Back Guarantee</p>
              </div>
            )}

            <div className="mt-6 pt-6 border-t">
              <h4 className="font-bold text-gray-800 mb-3">Instructor</h4>
              <div className="flex items-center">
                <img src={course.instructor.avatar_url || 'https://placehold.co/100x100/E2E8F0/4A5568?text=Avatar'} alt={course.instructor.name} className="w-12 h-12 rounded-full mr-4" />
                <div>
                  <h5 className="font-semibold text-gray-900">{course.instructor.name}</h5>
                  <p className="text-sm text-gray-600">{course.instructor.title}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CourseDetail;
